
      .sort {
  
        select {
          background-color: #fff;
          outline: none;
          border: 1px solid #ececec;
          border-radius: 2px;
          margin-left: 10px;
          width: auto;
          height: 35px;
          cursor: pointer;
  
          &:hover {
            border: 1px solid #5b5a5e;
          }
        }
      }