.filters {
    width: 15%;
    float: left;
  
    &-available-size {
      display: inline-block;
      margin-bottom: 10px;
      /* Customize the label (the container) */
      label {
        display: inline-block;
        position: relative;
        cursor: pointer;
        font-size: 22px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 35px;
        height: 35px;
        font-size: 1em;
        margin-bottom: 8px;
        margin-right: 8px;
        border-radius: 50%;
        line-height: 35px;
        text-align: center;
  
        /* On mouse-over, add a grey background color */
        &:hover input ~ .checkmark {
          border: 1px solid #1b1a20;
        }
  
        /* When the checkbox is checked, add a blue background */
        & input:checked ~ .checkmark {
          background-color: #1b1a20;
          color: #ececec;
        }
  
        /* Show the checkmark when checked */
        & input:checked ~ .checkmark:after {
          display: block;
        }
  
        input {
          position: absolute;
          opacity: 0;
          cursor: pointer;
        }
  
       
        .checkmark {
          position: absolute;
          top: 0;
          left: 0;
          width: 35px;
          height: 35px;
          font-size: 0.8em;
          border-radius: 50%;
          line-height: 35px;
          text-align: center;
          color: #1b1a20;
          background-color: #eabf22;
          border: 1px solid transparent;
        }
      }
    }
  }
  